import { Button, Form, Input, message } from "antd";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ButtonLogin, Circle, FormLogin, InputCustom, LabelCustom, LoginWrapper, Title } from "./styles";


const LoginFormUser = () => {
    const [form] = Form.useForm();
    const navigate = useNavigate();

    const onSubmit = async () => {
        const data = await form.validateFields();
        if(data.username === "admin" && data.password === "admin123"){
            localStorage.setItem("token",data.username + " loged in");
            navigate("/users")
        }
        else{
            message.error("Tn ngi dng hoc mt khu khng ng");
        }
    }

    return(
        <div>
        <LoginWrapper>
            <Title>LOG IN</Title>
            <FormLogin form={form} layout="vertical">
                <LabelCustom>Username</LabelCustom>
                <Form.Item name="username" rules={[{required: true, message:"Tn ngi dng l bt buc"}]}>
                    <InputCustom/>
                </Form.Item>
                <LabelCustom>Mt khu</LabelCustom>
                <Form.Item name="password" rules={[{required: true, message:"Mt khu l bt buc"}]}>
                    <InputCustom type="password"/>
                </Form.Item>
            </FormLogin>
            <ButtonLogin onClick={onSubmit}>Log in</ButtonLogin>
        </LoginWrapper>
        {/* <Circle pos="top"/>
        <Circle pos="bottom"/> */}
        </div>     
    );
}

export default LoginFormUser;