import { Form, Modal, Input, InputNumber, Select } from "antd";
import { useEffect } from "react";


const ModalFormUsers = ({open, onCancel, onSubmit,formData, loading}) => {
    const [form] = Form.useForm();

    const onCreate = async () => {
        const data = await form.validateFields();
        onSubmit(formData.id,data);
    }

    useEffect(()=>{
        if(!open){
            form.resetFields();
        }
    },[open])

    useEffect(()=>{
        if(open && formData.id){
            form.setFieldsValue(formData);
            console.log(formData);
        }
    },[open,formData])

    return(
        <Modal confirmLoading={loading} open={open} onCancel={onCancel} onOk={onCreate}>
            <Form form={form} layout="vertical">
                <Form.Item label="Name" name="name" rules={[{required:true,message:"Tn ngi dng l bt buc!"}]}>
                    <Input placeholder="Tn ngi dng"/>
                </Form.Item>
                <Form.Item label="Email" name="email" rules={[{required:true,message:"Email l bt buc!"},{type:"email", message:"Email khng ng nh dng"}]}>
                    <Input placeholder="Email"/>
                </Form.Item>
                <Form.Item label="Phone" name="phone" rules={[{required:true,message:"S in thoi l bt buc!"},{type:"number", message:"S in thoi phi l s"}]}>
                    <InputNumber placeholder="S in thoi"/>
                </Form.Item>
                <Form.Item label="Status" name="status" rules={[{required:true,message:"Trng thi l bt buc!"}]}>
                    <Select options={[{label:"Active",value: true},{label:"Inactive", value:false}]}/>
                </Form.Item>
                <Form.Item label="Avartar" name="avatar" rules={[{required:true,message:"Hnh nh l bt buc"}]}>
                    <Input placeholder="nh"/>
                </Form.Item>
            </Form>
        </Modal>
    )
}

export default ModalFormUsers;