import { Form, Modal, Input, InputNumber, Select } from "antd";
import { useEffect } from "react";


const ModalFormProducts = ({open, onCancel, onSubmit,formData, loading}) => {
    const [form] = Form.useForm();

    const onCreate = async () => {
        const data = await form.validateFields();
        onSubmit(formData.id,data);
    }

    useEffect(()=>{
        if(!open){
            form.resetFields();
        }
    },[open])

    useEffect(()=>{
        if(open && formData.id){
            form.setFieldsValue(formData);
            console.log(formData);
        }
    },[open,formData])

    return(
        <Modal confirmLoading={loading} open={open} onCancel={onCancel} onOk={onCreate}>
            <Form form={form} layout="vertical">
                <Form.Item label="Name" name="name" rules={[{required:true,message:"Tn sn phm l bt buc!"}]}>
                    <Input placeholder="Tn sn phm"/>
                </Form.Item>
                <Form.Item label="Code" name="code" rules={[{required:true,message:"M sn phm l bt buc!"}]}>
                    <Input placeholder="M sn phm"/>
                </Form.Item>
                <Form.Item label="Price" name="price" rules={[{required:true,message:"Gi sn phm l bt buc!"},{type:"number", message:"S in thoi phi l s"}]}>
                    <InputNumber placeholder="Gi sn phm"/>
                </Form.Item>
                <Form.Item label="Description" name="description" rules={[{required:true,message:"m t l bt buc!"}]}>
                    <Input placeholder="M t"/>
                </Form.Item>
                <Form.Item label="Thumnail" name="Thumbnail" rules={[{required:true,message:"nh sn phm l bt buc"}]}>
                    <Input placeholder="nh sn phm"/>
                </Form.Item>
            </Form>
        </Modal>
    )
}

export default ModalFormProducts;